@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,500&display=swap);
*{box-sizing:border-box}html{font-family:"Roboto", sans-serif}body{margin:0}h1{margin:0}ul{padding:0;margin:0}li{list-style-type:none}.app{max-width:600px;margin:0 auto;padding:40px;background-color:#e4e4e4;border-radius:10px}.app__buttons{display:flex;justify-content:space-between;margin:10px 0}.app__button{width:160px;height:40px;background-color:#158467;color:white;border-radius:5px;border:none;outline:none;font-size:16px;transition:background-color 0.3s}.app__button:hover{background-color:#065f4f;cursor:pointer}.app__goodList{display:grid;grid-gap:10px;gap:10px}

